body {
    background-color: #2398ff;
    background-image:
        radial-gradient(#0d7eff 10%, transparent 0), 
        radial-gradient(#0d7eff 10%, transparent 0);
    background-size: 30px 30px;
    background-position: 0 0, 15px 15px;
    margin: 0 0;
}

h1 {
    font-family: 'Gloria Hallelujah', cursive;
    font-size: 60px;
    color: white;
    -webkit-text-fill-color: white;
    -webkit-text-stroke-width: 3px;
    -webkit-text-stroke-color: #2D2B29;
}

h2 {
    font-family: 'Gloria Hallelujah', cursive;
    font-size: 38px;
    color: #2D2B29;
}

p {
    margin: 0 2vw;
    font-family: 'EB Garamond', normal;
    font-size: 24px;
    color: #2D2B29;
}

a {
    text-decoration: none;
    color: inherit;
}

section {
    width: 100%;
}

article {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    padding-bottom: 3vw;
}

span {
    font-family: 'EB Garamond', normal;
    color: white;
}

.link {
    color: #57c9e9;
}

/* Smart Phones */
@media screen and (max-width: 40em){
    h1 {
        font-size: 30px;
        -webkit-text-stroke-width: 2px;
    }
    h2 {
        text-align: center;
        font-size: 22px;
    }

    article {
        flex-flow: column wrap;
        align-items: none;
    }

    p {
        font-size: 18px;
    }

    .articlePicture{
        width: 60vw;
    }
}
header {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

nav {
    width: 100vw;
    display: flex;
    justify-content: center;
    background-color: #54a6ff;
    background-image:
        radial-gradient(#63adff 20%, transparent 0), 
        radial-gradient(#63adff 20%, transparent 0);
    background-size: 30px 30px;
    background-position: 0 0, 15px 15px;
    border-top: 4px solid #86c6ff;
    border-bottom: 4px solid #86c6ff;
}

nav ul {
    padding: 0;
    width: 80vw;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
}

nav ul li {
    width: 150px;
    display: flex;
    justify-content: center;
    font-family: 'Cairo', sans-serif;
    font-size: 2vw;
    font-weight: bold;
    color: #ffffff;
    list-style-type: none;
    border: 4px solid transparent;
    border-radius: 10px;
}

nav ul li:hover {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}

.center {
    width: 100vw;
    display: flex;
    justify-content: center;
}

.nameAndLogo {
    width: 500px;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
}

.navHome {
    -webkit-transform:rotate(-5deg);
            transform:rotate(-5deg);
    background-color: #d88986;
    border-color: #ebbbb8;
}

.navAbout {
    -webkit-transform:rotate(3deg);
            transform:rotate(3deg);
    background-color: #7bc572;
    border-color: #a7df9f;
}

.navProjects {
    -webkit-transform:rotate(2deg);
            transform:rotate(2deg);
    background-color: #ded15e;
    border-color: #e7df95;
}

.navMeetups {
    -webkit-transform:rotate(-2deg);
            transform:rotate(-2deg);
    background-color: #3c5eab;
    border-color: #4878e7;
}

.navHome:hover {
    background-color: #e7b3b1;
}

.navAbout:hover {
    background-color: #c3f0be;
}

.navProjects:hover {
    background-color: #e7e2b3;
}

.navMeetups:hover {
    background-color: #4670d1;
}


/* Smart Phones */
@media screen and (max-width: 40em){
    nav ul {
        width: 100vw;
    }
    nav ul li {
        font-size: 5vw;
    }

    .nameAndLogo {
        width: 380px;
        justify-content: flex-start;
    }

    .navHome, .navAbout, .navProjects, .navMeetups {
        -webkit-transform:rotate(0deg);
                transform:rotate(0deg);
        width: 47vw;
        border-radius: 0;
    }

    .navPenguin {
        display: none;
        visibility:hidden;
    }
}

.aboutMe {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: space-between;
}

.resumeButton {
    background: #1fc186cc;
    background: linear-gradient(to right, #8df24bcc , #1fc186cc);
    border-color: rgba(178, 209, 159, 0.8);
    font-family: 'Cairo', sans-serif;
    font-weight: bold;
    font-size: 30px;
    line-height: 0px;
    color: white;;
    height: 55px;
    width: 300px;
    border-radius: 100px;
    border-width: 1px;
}

/* Smart Phones */
@media screen and (max-width: 40em){
    .resumeButton {
        margin-top: 20px;
        width: 100%;
        font-size: 24px;
        border-radius: 0;
        border-width: 3px;
    }
}
main {
    margin: 20px 10vw;
    padding: 10px 10vw; 
    background:url(
        data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAFCAYAAACNbyblAAAAIUlEQVQYV2P8////fwY0wAgTfPiDgUGeAyILF0RWTLwgAEuZFfLl0hmXAAAAAElFTkSuQmCC) repeat;
    border: solid 10px white;
    border-radius: 2px;
}

/* Smart Phones */
@media screen and (max-width: 40em){
    main {
        margin: 20px 0;
        padding: 10px 0;
    }
}
footer {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    background: #2D2B29;
    width: 100%;
    height: 60px;
    text-align: center;
    font-size: 18px;
}
